import React from 'react';
import { jsonFormatter } from '../utilities/jsonFormatter';
import swal from 'sweetalert2';

const Footer = ({ jsonContent, setFormattedJson }) => {
  const handleButtonClick = () => {
    if (jsonContent !== '') {
      let formattedData = jsonFormatter(jsonContent);
      setFormattedJson({ labels: formattedData[1], datasets: formattedData[0] });
      return console.log('Pressed', formattedData);
    } else {
      swal
        .fire({
          title: 'Invalid data!',
          text: 'Please, insert all kinds of data required!',
          icon: 'error'
        })
        .then(() => {
          window.location.reload();
        });
    }
  };

  return (
    <div className="footer">
      <button onClick={() => handleButtonClick()}>Generate Chart</button>
    </div>
  );
};

export default Footer;
