import { parse } from 'dirty-json';
import _ from 'lodash';
import swal from 'sweetalert2';

export const jsonFormatter = (input) => {
  const showErrorAlert = () => {
    swal
      .fire({
        title: 'Invalid data!',
        text: 'Please, insert all kinds of data required!',
        icon: 'error'
      })
      .then(() => {
        // safeguard to avoid crashing, it forces the page to reload
        window.location.reload();
      });
  };

  // Get labels from start & stop
  const timestampConverter = (start, stop) => {
    const startTime = convert(start);
    const stopTime = convert(stop);

    const convert = (time) => {
      let data = new Date(time);
      let minutes = String(data.getMinutes()).padStart(2, '0');
      let seconds = String(data.getSeconds()).padStart(2, '0');

      return `${minutes}:${seconds}`;
    };

    return [`${startTime}`, `${stopTime}`];
  };

  if (input !== '') {
    const inputArray = input.split('\n').filter((value) => value !== '');
    const objectsArray = inputArray.map((value) => {
      try {
        return parse(value.replace('},', '}'));
      } catch (err) {
        showErrorAlert();
        return [null, null];
      }
    });

    const [start] = objectsArray.filter((object) => object.type === 'start');
    const [stop] = objectsArray.filter((object) => object.type === 'stop');
    const [span] = objectsArray.filter((object) => object.type === 'span');
    const data = objectsArray.filter((object) => object.type === 'data');

    if (start === undefined || stop === undefined || span === undefined || data === []) {
      showErrorAlert();
      return [null, null];
    }
    const labels = timestampConverter(start.timestamp, stop.timestamp);
    const acceptedData = validateData(data, span.begin, span.end);
    const cleanedData = cleanData(acceptedData);

    // At this point I crashed when I tried to group the data, something like
    // "Linux Chrome: {"xxx","xxx"}"
    // so I decided to search for a library to manipulate the data, and I founded lodash.
    // I saw that i could use from the beginning but I decided not to refactor the
    // functions for a better appreciation.
    const groupedData = _.groupBy(cleanedData, (data) => data.title);

    const minResponseData = createMinArray(Object.values(groupedData));
    const maxResponseData = createMaxArray(Object.values(groupedData));

    const dataset = createDataset([...minResponseData, ...maxResponseData]);
    return [dataset, labels];
  } else {
    showErrorAlert();
  }
};

// Function to check if data timestamp is between 'begin' and 'end' timestamp.
const validateData = (data, begin, end) => {
  const acceptedData = data.filter((object) => object.timestamp >= begin && object.timestamp <= end);
  return acceptedData;
};

// Clear data to make logic to insert on the chart
const cleanData = (data) => {
  const showWarningAlert = (missing) => {
    swal.fire({
      title: 'Missing data!',
      text: `Please, insert all ${missing} input required to get a proper result.`,
      icon: 'warning'
    });
  };
  const obj = data.map((object) => {
    let os = '';
    let browser = '';
    if (object.os) {
      os = capitalizeStrings(object.os);
    } else {
      os = '(MISSING)';
      showWarningAlert('operational system');
    }
    if (object.browser) {
      browser = capitalizeStrings(object.browser);
    } else {
      browser = '(MISSING)';
      showWarningAlert('browser');
    }
    let minTime = object.min_response_time;
    let maxTime = object.max_response_time;

    return {
      title: `${os} ${browser}`,
      min_response_time: minTime,
      max_response_time: maxTime,
      timestamp: object.timestamp
    };
  });

  return obj;
};

// Function to capitalize data. Ex: from "linux" to "Linux"
const capitalizeStrings = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

// Function to create the array with the min_response_values
const createMinArray = (values) => {
  const minValues = values.map((object) => {
    const obj = { ...object };
    let data;
    let arrayWithMinresponsetimeValues = [];

    for (const key in obj) {
      if (Object.hasOwnProperty.call(obj, key)) {
        const element = obj[key];
        arrayWithMinresponsetimeValues.push(element.min_response_time);
      }
    }
    // getting min_response_time from minor to higher
    let [minor, biggest] = getMinMaxValues(arrayWithMinresponsetimeValues);

    data = [{ x: minor }, { x: biggest }];

    return {
      label: `${obj[0].title} ${Object.keys(obj[0])[1]}`,
      data,
      timestamp: [object[0].timestamp, object[object.length - 1].timestamp]
    };
  });

  return minValues;
};

// Function to create the array with the min_response_values
const createMaxArray = (values) => {
  const maxValues = values.map((object) => {
    const obj = { ...object };
    let data;
    let arrayWithMaxresponsetimeValues = [];

    for (const key in obj) {
      if (Object.hasOwnProperty.call(obj, key)) {
        const element = obj[key];
        arrayWithMaxresponsetimeValues.push(element.max_response_time);
      }
    }

    let [minor, biggest] = getMinMaxValues(arrayWithMaxresponsetimeValues);

    data = [{ x: minor }, { x: biggest }];

    return {
      label: `${obj[0].title} ${Object.keys(obj[0])[2]}`,
      data,
      timestamp: [object[0].timestamp, object[object.length - 1].timestamp]
    };
  });

  return maxValues;
};

// Return the dataset object, the objects to plot the chart
const createDataset = (data) => {
  const charData = data.map((object) => {
    // Generating random color: Generate a random number | Turn to hexadecimal | Add six '0' to fill
    // the hexadecimal color if it was necessary (needs to have 6 character)
    const randomColor =
      '#' +
      Math.floor(Math.random() * 2 ** 24)
        .toString(16)
        .padStart(6, '0');

    return {
      label: object.label,
      data: [
        { x: object.timestamp[0], y: object.data[0].x },
        { x: object.timestamp[1], y: object.data[1].x }
      ],
      backgroundColor: randomColor,
      borderColor: randomColor,
      pointBorderWidth: 5,
      pointHoverRadius: 5,
      borderWidth: 4
    };
  });

  const dataset = [];
  charData.forEach((object) => {
    dataset.push(object);
  });

  return dataset;
};

// Auxiliary function to return the biggest and the minor value from an array
const getMinMaxValues = (array) => {
  let biggest = array.reduce(function (a, b) {
    return Math.max(a, b);
  });

  let minor = array.reduce(function (a, b) {
    return Math.min(a, b);
  });

  return [minor, biggest];
};
